import React from 'react';
import { Route, NavLink, Link } from 'react-router-dom';
import connect from '@redux/connect';
import Nav from '@js/nav';
// ===================================================================== 
import Router from './router';
import logo from '@images/logo.png';
// ===================================================================== ant
//  antd  css 
//import 'antd/dist/antd.css';
// =====================================================================
class ReactIndex extends React.Component {
	componentDidMount(){
		Nav()
	}
	render(){
		return (
			<div className="full-screen">
				<header className="header">
					<h1 className="logo"><Link to="/antd"><img src={logo} alt=""/></Link></h1>
					<i className="font-navicon small-nav" id="smallNav"></i>
				</header>
				<section className="container">
					<aside className="navigation oys" id="navigation">
						<ul>
							<li>
								<h3 className="js-tit"><span> Button</span><i className="font-angle-right"></i></h3>
								<menu>
									<li><NavLink to="/antd/button/button-base" activeClassName="active"></NavLink></li>
								</menu>
							</li>
							<li>
								<h3 className="js-tit"><span> Icon</span><i className="font-angle-right"></i></h3>
								<menu>
									<li><NavLink to="/antd/icon/icon-base" activeClassName="active"></NavLink></li>
									<li><NavLink to="/antd/icon/icon-all" activeClassName="active"></NavLink></li>
								</menu>
							</li>
						</ul>
					</aside>
					<main className="oxys">
						<section className="content">
							{/* ng router-outletvue router-view */}
							<Route component={Router} /> 
						</section>
					</main>
				</section>
			</div>
		)
	}
}

export default connect(ReactIndex)



